import { useEffect } from 'react';
import { Container } from "@mui/material";



const App = ({ client }) => {

  useEffect(() => {
    client.apiCall('get', '/hi')
      .then(({ data }) => console.log(data.message) )
  })

  return (
    <Container>
      <p>Read the README.md</p>
    </Container>
  )
}

export default App;